import React, { Component } from 'react';

export default class VideoContent extends Component {

    state = {
        firstLoad: false,
        playBtnRed: false,
        className: "",
        videoId: "",
        width: 0,
        height: 0,
        styleImg: {},
        styleIframe: {},
        thumbnailQuality: "",
        showContent: false
    };

    render() {
        const { src = "", params = {}} = this.props;

        if (!this.state.firstLoad) {

            let localState = this.state;

            const thumbnailQuality = {default: "default", hq: "hqdefault", mq: "mqdefault", sd: "sddefault", maxres: "maxresdefault"};
            let findInUrl = false;
            if (src.indexOf("youtube") !== -1 && src.indexOf("watch") !== -1) {
                findInUrl = true;
                try {
                    localState.videoId = new URLSearchParams(new URL(src).search).get("v");
                } catch {
                    try {
                        localState.videoId = new URLSearchParams(new URL("https://"+src).search).get("v");
                    } catch {return(<div>error loading, wrong URL...</div>)}};
            };

            if (src.indexOf("youtube") !== -1 && src.indexOf("embed") !== -1) {
                findInUrl = true;
                try {
                    localState.videoId = new URL(src).pathname.replace('/embed/', '');
                } catch {
                    try {
                        localState.videoId = localState.videoId = new URL("https://"+src).pathname.replace('/embed/', '');
                    } catch {return(<div>error loading, wrong URL...</div>)}};
            };

            if (src.indexOf("youtu.be") !== -1) {
                findInUrl = true;
                try {
                    localState.videoId = new URL(src).pathname.replace('/', '');
                } catch {
                    try {
                        localState.videoId = new URL("https://"+src).pathname.replace('/', '');
                    } catch {return(<div>error loading, wrong URL...</div>)}};
            };
            
            if (!findInUrl) {
                localState.videoId = src;
            } else {
                if (!this.state.videoId) {
                    localState.firstLoad = true;
                    return(<div>error loading, not found video ID...</div>);
                };
            };

            if (params.hasOwnProperty("height")) {
                localState.height = params.height;
            } else {
                localState.height = 60;
            };
            if (params.hasOwnProperty("width")) {
                localState.width = params.width;
            } else {
                localState.width = 120;
            };
            if (params.hasOwnProperty("title")) {
                localState.title = params.title;
            } else {
                localState.title = "Video from YouTube";
            };
            if (params.hasOwnProperty("className")) {
                localState.className = params.className;
            } else {
                localState.className = "VideoContent__"+this.state.videoId;
            };
            if (params.hasOwnProperty("thumbnailQuality")) {
                localState.height = params.height;
                if (params.thumbnailQuality === "auto") {
                    if (localState.width.indexOf("px") !== -1) {
                        try {
                            if (parseInt(localState.width) <= 120) {
                                localState.thumbnailQuality = "default"; 
                            } else if (parseInt(localState.width) <= 320) {
                                localState.thumbnailQuality = "mqdefault"; 
                            } else if (parseInt(localState.width) <= 480) {
                                localState.thumbnailQuality = "hqdefault"; 
                            } else if (parseInt(localState.width) > 480) {
                                localState.thumbnailQuality = "maxresdefault"; 
                            }; 
                        } catch {};
                    }; 
                } else {
                    try {
                        localState.thumbnailQuality = thumbnailQuality[params.thumbnailQuality];
                    } catch {
                        localState.thumbnailQuality = "maxresdefault";
                    };
                };
            } else {
                localState.thumbnailQuality = "maxresdefault";
                if (localState.width.indexOf("px") !== -1) {
                    try {
                        if (parseInt(localState.width) <= 120) {
                            localState.thumbnailQuality = "default"; 
                        } else if (parseInt(localState.width) <= 320) {
                            localState.thumbnailQuality = "mqdefault"; 
                        } else if (parseInt(localState.width) <= 480) {
                            localState.thumbnailQuality = "hqdefault"; 
                        } else if (parseInt(localState.width) > 480) {
                            localState.thumbnailQuality = "maxresdefault"; 
                        }; 
                    } catch {};
                };
            };
            localState.styleImg = {
                backgroundPosition: 'center center, center center', 
                backgroundRepeat: 'no-repeat, no-repeat', 
                // backgroundImage: "url(https://img.youtube.com/vi/"+this.state.videoId+"/"+this.state.thumbnailQuality+".jpg)", 
                backgroundImage: "url("+this.state.playImgIdle+"), url(https://img.youtube.com/vi/"+this.state.videoId+"/"+this.state.thumbnailQuality+".jpg)", 
                // backgroundSize: 'calc(min('+this.state.width+'px, 100%))',
                backgroundSize: '70px 60px, cover',
                maxWidth: "100%",
                cursor: "pointer",
                width: this.state.width,
                height: this.state.height
                // , '+this.state.width+' '+this.state.height+'
                // width: 'calc(('+this.state.width+'px-(('+this.state.width+'px/2.2)*2))-80%)',  
                // width: this.state.width-((this.state.width/2.2)*2)+'px', 
                // height: this.state.height-((this.state.height/2.4)*2)+'px',
                // padding: this.state.height/2.4+'px '+this.state.width/2.2+'px',
              };
            localState.styleImg = {position: "relative", width: this.state.width, height: this.state.height, backgroundColor: "#000000"};
            if (params.hasOwnProperty("styleImg")) {
                localState.styleImg = Object.assign({}, localState.styleImg, params.styleImg);
            };
            localState.styleIframe = {maxWidth: "100%"};
            if (params.hasOwnProperty("styleIframe")) {
                localState.styleIframe = Object.assign({}, localState.styleIframe, params.styleIframe);
            };
            if (params.hasOwnProperty("playImgIdle")) {
                localState.playImgIdle = params.playImgIdle;
            };
            if (params.hasOwnProperty("playImgHover")) {
                localState.playImgHover = params.playImgHover;
            };

            localState.firstLoad = true;

        };
        
        if (this.state.showContent) {
            return (
                <iframe className={this.state.className+"__iframe"} key={"vc_"+this.state.videoId+"__iframe"} title={this.state.title} alt={this.state.title} src={"https://www.youtube.com/embed/"+this.state.videoId+"?rel=false&showinfo=false&autoplay=true"} style={this.state.styleIframe} allow='autoplay; encrypted-media' width={this.state.width} height={this.state.height} frameborder={0} />
            );
        } else {
            return (
                // // src={this.state.playImgIdle}
                // <img className={this.state.className+"__img"} key={"vc_"+this.state.videoId+"__img"} title={this.state.title} alt={this.state.title} style={this.state.styleImg} src="data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAAABCAQAAAC1HAwCAAAAC0lEQVR42mNkYAAAAAYAAjCB0C8AAAAASUVORK5CYII="
                // //   onMouseOver={(e) => (e.currentTarget.style.backgroundImage.src = this.state.playImgPlay)}
                // onMouseOver={(e) => (e.currentTarget.style.backgroundImage = "url("+this.state.playImgPlay+"), url(https://img.youtube.com/vi/"+this.state.videoId+"/"+this.state.thumbnailQuality+".jpg)")}
                // //   onMouseOut={(e) => (e.currentTarget.src = this.state.playImgIdle)}
                // onMouseOut={(e) => (e.currentTarget.style.backgroundImage = "url("+this.state.playImgIdle+"), url(https://img.youtube.com/vi/"+this.state.videoId+"/"+this.state.thumbnailQuality+".jpg)")}
                //   onClick={() => {this.setState({showContent: true})}} 
                //   />
                // style={{width: this.state.width, height: this.state.height}}
                <div className={this.state.className+"__img"} key={"vc_"+this.state.videoId+"__img"} style={this.state.styleImg}>
                    <picture>
                        <source srcSet= {"https://i.ytimg.com/vi_webp/"+this.state.videoId+"/"+this.state.thumbnailQuality+".webp"} type="image/webp"/>
                        <img className={this.state.className+"__media"} key={"vc_"+this.state.videoId+"__media"} title={this.state.title} style={{width: this.state.width, height: this.state.height}} src={"https://i.ytimg.com/vi/"+this.state.videoId+"/"+this.state.thumbnailQuality+".jpg"} alt="1. Пилот, разборы, ответы и лайвы"/>
                    </picture>
                    <button className={this.state.className+"__button"} key={"vc_"+this.state.videoId+"__button"} style={{position:"absolute",top:"50%",left:"50%",zIndex:"1",padding:"0",width:"68px",height:"48px",border:"none",backgroundColor:"transparent",transform:"translate(-50%, -50%)",cursor:"pointer"}} type="button" aria-label="Play video" onMouseOver={(e) => (this.setState({playBtnRed: true}))} onMouseOut={(e) => (this.setState({playBtnRed: false}))} onClick={() => {this.setState({showContent: true})}} >
                        <svg width="68" height="48" viewBox="0 0 68 48"><path className={this.state.className+"__button-shape"} key={"vc_"+this.state.videoId+"__button-shape"} style={{fill: this.state.playBtnRed ? "#ff0000" : "#212121",fillOpacity: this.state.playBtnRed ? "0.8" : "1"}} d="M66.52,7.74c-0.78-2.93-2.49-5.41-5.42-6.19C55.79,.13,34,0,34,0S12.21,.13,6.9,1.55 C3.97,2.33,2.27,4.81,1.48,7.74C0.06,13.05,0,24,0,24s0.06,10.95,1.48,16.26c0.78,2.93,2.49,5.41,5.42,6.19 C12.21,47.87,34,48,34,48s21.79-0.13,27.1-1.55c2.93-0.78,4.64-3.26,5.42-6.19C67.94,34.95,68,24,68,24S67.94,13.05,66.52,7.74z"></path><path className={this.state.className+"__button-icon"} key={"vc_"+this.state.videoId+"__button-icon"} style={{fill: "#ffffff"}} d="M 45,24 27,14 27,34"></path></svg>
                    </button>
                </div>
            );
        }

    };
};

